<template>
  <div class="login">
    <Row type="flex" justify="center" align="middle" class="code-row-bg">
      <Form class="loginForm" ref="formCustom" :model="formCustom" :rules="ruleCustom" :label-width="80">
        <FormItem label="username" prop="username">
          <Input type="text" v-model="formCustom.username" :autofocus="focus"/>
        </FormItem>
        <FormItem label="Password" prop="password">
          <Input type="password" v-model="formCustom.password"  on-enter="handleSubmit('formCustom')"/>
        </FormItem>
        <FormItem>
          <Button type="primary" @click="handleSubmit('formCustom')">登陆</Button>
          <Button @click="handleRegist" style="margin-left: 30px">注册</Button>
        </FormItem>
      </Form>
    </Row>

  </div>
</template>

<script>
  export default {
    name: "Login",
    data() {
      return {
        focus:true,
        formCustom: {
          username: '',
          password: '',

        },
        ruleCustom: {
          username: [
            {required: true, message: "用户名不能为空", trigger: "blur"}
          ],
          password: [
            {required: true, message: "密码不能为空", trigger: 'blur'}
          ]
        }
      }
    },
    methods: {
      handleRegist(){
        console.log("跳转到注册")
        this.$router.push('/register')
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            var params = new URLSearchParams();
            params.append('userName', this.formCustom.username);
            params.append('userPassword', this.formCustom.password);
            this.axios.post('login', params).then(response => {
              if (response.data.code ==100) {
                sessionStorage.setItem("userInfo",JSON.stringify(response.data.map.user));
                this.$Message.success('登陆成功!');
                this.$store.commit('setLogin')
                  //this.$router.back()//需要修改，获取上一页面 的路由，判断，然后跳转
                if(this.$store.state.fromPath==='/register'){  //如果是从注册页面来的话直接返回到根目录
                  this.$router.push('/')
                }else{
                  this.$router.back()
                }
              }else{
                this.$Message.error('用户名或密码错误请重试!');
              }
            }).catch(error => {
              this.$Message.warning('错误请重新登陆')
            })
          } else {
            this.$Message.error('Fail!');
          }
        })
      }
    }
  }
</script>

<style scoped>

  .login {
    position: relative;
    width: 100%;
    height: 1080px;
    background-image: url("../../../static/img/1.png");
    background-size: 100%;
  }

  .loginForm {
    width: 350px;
    height: 200px;
    background-color: rgba(255, 255, 255, 0.6);
    padding: 30px;
    margin: 10px;
    margin-top: 300px;
    border-radius: 5px;
  }

</style>



